import { addons } from '@storybook/addons'
import theme from '../src/ComponentsTheme'
import { STORY_CHANGED } from '@storybook/core-events'

addons.setConfig({
  theme,
})

addons.register('google-analytics', api => {
  api.on(STORY_CHANGED, storyName => {
    const { packageName } = api.getGlobals()
    // Prefix the package name (if found) to the page title
    const titlePrefix = packageName ? `${packageName}: ` : ''
    const title = `${titlePrefix}${storyName}`
    gtag('event', 'page_view', { page_title: title })
  })
})
